@font-face {
  font-family: "font-1";
  src: url(../assets/font/Roboto/Roboto-Medium.ttf);
}

/* 
RESET CSS
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font-family: "font-1";
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* CSS */

.app-container h1 {
  font-size: 2.5rem;
  text-align: center;
  margin: 40px 0;
  color: salmon;
  font-weight: 100;
}

/* INPUT */

.search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search input {
  width: 15%;
  height: 30px;
  border: 2px solid salmon;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 50px;
  text-align: center;
}

/* CARD */

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 100px;
}

.card {
  display: flex;
  width: 15%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  border: 2px solid salmon;
  border-radius: 20px;
}

.card h2 {
  font-size: 1.5rem;
  font-weight: 100;
  text-align: center;
}

.card h3 {
  margin: 10px 0;
  color: salmon;
}

.card img {
  width: 80%;
  margin: 20px 0;
  border-radius: 20px;
}

.card p {
  width: 80%;
  text-align: justify;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 10;
  overflow: hidden;

}